import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ConciergeriePage } from './conciergerie.page';

const routes: Routes = [
  {
    path: '',
    component: ConciergeriePage
  },
  {
    path: 'conciergerie-historique',
    loadChildren: () => import('./conciergerie-detail/conciergerie-detail.module').then( m => m.ConciergerieDetailPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ConciergeriePageRoutingModule {}
